html {
  overflow-y: scroll;
  -webkit-backface-visibility: hidden;
  backface-visibility: initial;
}
html,
body {
  height: 100%;
  min-height: 100%;
}
body {
  line-height: 1.4em;
  font-family: "Open Sans", sans-serif;
  color: #292929;
  opacity: 0;
  -webkit-font-smoothing: subpixel-antialiased;
}
::selection {
  text-shadow: none;
  background: black;
  color: white;
}
::-moz-selection {
  text-shadow: none;
  background: black;
  color: white;
}
:focus {
  outline: none;
}
a {
  color: #888;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}
a.highlight:hover {
  color: #999;
}
@media (min-width: 1200px) {
  .container {
    width: 1191px;
  }
}

/*---------- HEROS ----------*/

/* Page Title Hero */
.page-hero .hero-content {
  padding-top: 55px;
}

.page-title-content {
  display: table-cell;
  height: 100%;
  vertical-align: middle;
  padding-top: 67px;
}
.page-title h1 {
  margin: 0;
}
.page-title hr {
  margin: 28px auto 32px auto!important;
}

/* Hero Containers */
.hero-fullscreen {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #0f1112;
}
.hero-fullwidth {
  position: relative;
  width: 100%;
  height: 70%;
  background-color: #0f1112;
}
.page-hero {
  position: relative;
  width: 100%;
  height: 45%;
  background-color: #0f1112;
}
.page-title {
  position: relative;
  width: 100%;
  height: 410px;
  background-color: #f5f5f5;
  color: #191a1c;
  display: table;
}
@media (max-width:992px) {
  .page-title {
    height: 300px;
  }
}
.hero-video {
  background-color: transparent;
  position: relative;
  overflow: hidden;
}
#vossen-youtube {
  position: absolute;
  background-color: #0f1112;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -9;
  overflow: hidden;
}

/* Map Parallax Hero */
.map-hero {
  position: relative;
  width: 100%;
  height: 55%;
  z-index: -1;
}
.map-parallax {
  position: fixed;
  width: 100%;
  height: 45%;
  z-index: -3;
}
@media (max-width:992px) {
  .hero-fullscreen {
    top: -57px!important;
    margin-bottom: -57px!important;
  }
  .hero-fullwidth {
    top: -57px!important;
    margin-bottom: -57px!important;
  }
  .page-title {
    margin-top: 0;
  }
}

/* Hero Content */
.hero-container {
  position: relative;
  display: table;
  height: 100%;
  text-align: center;
  margin: 0 auto;
  padding-top: 70px;
}
.hero-content {
  display: table-cell;
  height: 100%;
  vertical-align: middle;
}
@media (max-width:992px) {
  .hero-container {
    padding-top: 60px;
    top: 0!important;
  }
  .hero-content {
    text-align: center;
  }
  .scroll-opacity {
    opacity: 1!important;
  }
  .page-title-content {
    padding-top: 0;
  }
}



/* Hero Slider */
.hero-slider,
.hero-slider .owl-stage-outer,
.hero-slider .owl-wrapper-outer,
.hero-slider .owl-height,
.hero-slider .owl-stage,
.hero-slider .owl-wrapper,
.hero-slider .owl-item {
  height: 100% !important;
}
.hero-slider .owl-stage {
}
.hero-slider .slide {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  height: 100%!important;
}
.owl-item .appear {
  opacity: 0;
  margin-top: 100px!important;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
}
.owl-item .appear {
  -webkit-transition-delay: 0.5s;
  transition-delay: 0.5s;
}
.owl-item.active .appear {
  opacity: 1;
  margin-top: 0!important;
}
.owl-controls {
  margin-top: 0!important;
}
.navigation-thin.owl-theme .owl-controls .owl-nav div {
  color: #FFF;
  display: inline-block;
  padding: 20px;
  font-size: 42px;
  border-radius: 0;
  background: transparent;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
}
.navigation-thin.owl-theme .owl-controls .owl-nav div {
  position: absolute;
}
.navigation-thin.owl-theme .owl-controls .owl-nav div {
  opacity: 0;
}
.navigation-thin:hover.owl-theme .owl-controls .owl-nav div {
  opacity: 1;
}
.navigation-thin .owl-prev {
  margin-left: 40px!important;
}

.navigation-thin .owl-next {
  margin-right: 40px!important;
}
.navigation-thin:hover .owl-prev {
  opacity: .8;
  margin-left: 0!important;
}
.navigation-thin:hover .owl-next {
  opacity: .8;
  margin-right: 0!important;
}
.navigation-thin .owl-prev:hover,
.navigation-thin .owl-next:hover {
  opacity: 1;
}
.navigation-thin.owl-theme .owl-controls .owl-nav .owl-prev {
  left: 0;
  top: 50%;
  margin-top: -36px;
}
.navigation-thin.owl-theme .owl-controls .owl-nav .owl-next {
  right: 0;
  top: 50%;
  margin-top: -36px;
}

/* Section Bg Types */
.static {
  width: 100%;
  display: block;
  background-attachment: local;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.static-hero {
  background-position: center 0%;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  bottom:  0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.static-hero-content {
  position: relative;
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
}
.white-until-load {
  color: #FFF;
}
@media (max-width:992px) {
  .white-until-load {
    color: #191a1c;
  }
}
/* Parallax Backgrounds */
.parallax {
  background-size: cover;
  position: relative;
  background-position: 50% 50%;
  /*
  z-index: -1;*/
}
.overlay:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-image: url(../img/assets/dot-pattern.png);
  background-repeat: repeat;
  background-color: rgba(0,0,0,0.5);
  z-index: 0;
}
.parallax-bg {
  z-index: -9!important;
  //background: #191a1c;
  background: #ffffff;
}
.parallax-bg img {
  opacity: 0;
}
/* Content Slider */
.content-slider.owl-carousel .owl-item,
.content-slider.owl-carousel  .owl-item:active {
  cursor: default;
}
.owl-animated-in {
  -webkit-animation-delay: 1s;
  -moz-animation-delay: 1s;
  -o-animation-delay: 1s;
  -ms-animation-delay: 1s;
  animation-delay: 1s;
}
.owl-item p, .owl-item h5, .owl-item h1 {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.animated.bounceIn, .animated.bounceOut, .animated.flipOutX, .animated.flipOutY {
  animation-duration: 0.6s;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/*Home Scroll Button*/
.scroll-btn {
  height: 22px;
  width: 22px;
  position: absolute;
  display: block;
  left: 50%;
  bottom: 12px;
  margin-left: -11px;
  -webkit-transform: translateY(0);
  transform: translateY(0);
  -webkit-animation: scroll-btn-animation 1s infinite;
  -moz-animation: scroll-btn-animation 1s infinite;
  -o-animation: scroll-btn-animation 1s infinite;
  animation: scroll-btn-animation 2s infinite;
}
.scroll-btn i {
  font-size: 0.75em;
  color: #fff;
}
@keyframes scroll-btn-animation {
  0% { bottom: 0px; }
  30% { bottom: 12px; }
  60% { bottom: 4px; }
  90% { bottom: 12px; }
  100% { bottom: 0px; }
}
@-webkit-keyframes scroll-btn-animation {
  0% { bottom: 0px; }
  30% { bottom: 12px; }
  60% { bottom: 4px; }
  90% { bottom: 12px; }
  100% { bottom: 0px; }
}
@-moz-keyframes scroll-btn-animation {
  0% { bottom: 0px; }
  30% { bottom: 12px; }
  60% { bottom: 4px; }
  90% { bottom: 12px; }
  100% { bottom: 0px; }
}
@-o-keyframes scroll-btn-animation {
  0% { bottom: 0px; }
  30% { bottom: 12px; }
  60% { bottom: 4px; }
  90% { bottom: 12px; }
  100% { bottom: 0px; }
}

/*Owl Slider*/
.owl-dot {
  display: inline-block;
  padding: 6px 2px;
  position: relative;
}
.owl-theme .owl-controls .owl-dot span {
  width: 4px;
  height: 4px;
  margin: 2px 2px;
  display: block;
  background: rgba(255,255,255, .4);
  position: relative;
  z-index: 1000 !important;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  box-shadow: none;
  -webkit-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
  -moz-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
  -o-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
  -ms-transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
  transition: all 0.27s cubic-bezier(0.000, 0.000, 0.580, 1.000);
}
.owl-theme .owl-controls .owl-dot.active span {
  background: rgba(255,255,255, .9);
  border: none;
}
.owl-dots {
  text-align: center;
}
.pagination-in .owl-dots {
  display: block;
  width: 100%;
  position: absolute;
  bottom: 10px;
  left: 0;
  text-align: center;
  z-index: 100 !important;
}
.dark-pagination.owl-theme .owl-controls .owl-dot span {
  border: none;
  background: rgba(0,0,0, .3);
}
.dark-pagination.owl-theme .owl-controls .owl-dot.active span {
  border: none;
  background: rgba(0,0,0, .7);
}
.light-pagination.owl-theme .owl-controls .owl-dot span,
.light-pagination.owl-theme .owl-controls .owl-dot.active span {
  border: none;
  background: rgba(255,255,255, .3);
}
.white-pagination.owl-theme .owl-controls .owl-dot span,
.white-pagination.owl-theme .owl-controls .owl-dot.active span {
  border: none;
  background: rgba(255,255,255, .9);
}
.owl-carousel .owl-item {
  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  margin-right: -0.1px!important
}
/* Applies a "closed-hand" cursor during drag operation. */
.owl-carousel  .owl-item:active {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}


.play-btn i {
  position: relative;
  display: block;
  margin: auto;
  height: 100px;
  width: 100px;
  padding: 60px;
  border: 3px solid #fff;
  border-radius: 50em;
  transition: transform 0.3s;
  -webkit-transition: -webkit-transform 0.3s;
}
.play-btn i:before {
  position: absolute;
  top: 32%;
  left: 35%;
  font-size: 48px;
  color: #fff;
}
.play-btn i:hover {
  -webkit-transform: scale(0.95);
  transform: scale(0.95);
  -webkit-transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
}
.play-btn.dark i,
.play-btn.dark i:before {
  color: #191a1c;
  border-color: #191a1c;
}

/*---------- HEADERS ----------*/
nav {z-index: 3}
nav a {cursor: pointer}
@media (min-width:992px) {
  nav.navbar {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
    border-radius: 0;
    border: none;
    box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.1);
  }
  nav .nav {
    height: 69px;
    -webkit-transition: height 0.3s;
    -moz-transition: height 0.3s;
    transition: height 0.3s;
  }
  nav.nav-small .nav {
    height: 54px;
    -webkit-transition: height 0.3s;
    -moz-transition: height 0.3s;
    transition: height 0.3s;
  }
  nav>.container {
    padding-top: 0px;
  }
  nav.nav-small>.container {
    padding-top: 0px;
  }
  .navbar-default .navbar-nav>li {
    display: table;
    height: 100%;
  }
  .navbar-default .navbar-nav>li>a {
    color: #333;
    line-height: 2;
    display: table-cell !important;
    vertical-align: middle;
  }
  .navbar-brand img,
  .nav-small .navbar-brand img {
    -webkit-transition: max-height 0.4s, opacity 0.3s;
    -moz-transition: max-height 0.4s, opacity 0.3s;
    -o-transition: max-height 0.4s, opacity 0.3s;
    -ms-transition: max-height 0.4s, opacity 0.3s;
    transition: max-height 0.4s, opacity 0.3s;
  }
  .nav>li>a {
    display: inline-block;
    font-family: 'lovelo',sans-serif;
    font-size: 12.4px;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding-bottom: 8px;
  }
  .navbar-brand {
    height: auto;
    margin-left: 0!important;
    position: absolute;
    z-index: 999;
  }
  .navbar-brand>img {
    position: absolute;
    padding-left: 10px;
  }
  .container>.navbar-collapse {
    padding: 0;
  }
  /* Menu Positioning */
  .navbar-header>.container {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .navbar-collapse>.container {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .menu-right {
    float: right !important;
  }
  .menu-left {
    float: left;
    margin: 0;
  }
  .menu-center {
    float: none;
    margin: 0 auto;
    text-align: center;
  }
  .menu-center>li {
    display: inline-block;
    float: none;
  }
  .label-items-in-cart {
    position: absolute;
    width: 14px;
    height: 14px;
    padding: 3px 0 0 5px;
    font-family: "Open Sans", sans-serif;
    font-weight: 900;
    font-size: 8px;
    color: #fff;
    border-radius: 4em;
    line-height: 1;
    top: 18px;
    right: 1px;
    -webkit-transition: top 0.3s;
    -moz-transition: top 0.3s;
    transition: top 0.3s;
  }
  .nav-small .label-items-in-cart {
    top: 13px;
  }
  .nav li.nav-icon:last-child {
    margin-right: 10px;
  }
  .nav li.nav-icon a {
    padding: 16px 8px 14px 8px;
  }
  .nav li.nav-icon a i {
    font-size: 16px;
    line-height: 0;
    padding: 0;
  }
  .nav li.nav-icon .hidden-md {
    display: none;
  }
  .nav li.nav-separator {
    line-height: 0;
    height: 69px;
    margin: 0 16px 0 16px;
    border-left: 1px solid rgba(0,0,0, 0.1);
    transition: height 0.3s;
  }
  nav.nav-small .nav li.nav-separator {
    height: 54px;
    border-left: 1px solid rgba(0,0,0, 0.1);
    transition: height 0.3s;
  }

  /* Menu Hover Dropdown */
  .dropdown:hover>.dropdown-menu {
    display: block;
  }
  .dropdown-menu>li>a {
    padding: 12px 15px;
  }
  .dropdown > ul.dropdown-menu {
    visibility: hidden;
    display: block;
    opacity: 0;
    -webkit-transition: opacity 0.3s, visibility 0.3s;
    -moz-transition: opacity 0.3s, visibility 0.3s;
    -o-transition: opacity 0.3s, visibility 0.3s;
    -ms-transition: opacity 0.3s, visibility 0.3s;
    transition: opacity 0.3s, visibility 0.3s;
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
  }
  .dropdown:hover > ul.dropdown-menu {
