  text-align: center
}
.cart .quantity {
  height: auto;
  width: auto;
  max-width: 115px;
  margin-right: 20px!important;
}
.cart input.qty {
  position: relative;
  float: left;
  height: 60px;
  width: 60px;
  line-height: 56px;
  margin: 0;
  padding: 0 0 0 5px;
  box-shadow: none;
  border-radius: 0;
  text-shadow: none;
  border: 2px solid #e3e3e3;
  border-width: 2px!important;
  z-index: 1;
}
.cart input[type=number]::-webkit-inner-spin-button,
.cart input[type=number]::-webkit-outer-spin-button {
  opacity: 1;
}
.cart .product-remove .remove {
  float: right;
  padding: 0 10px;
  line-height: 42px;
  height: 44px;
  width: 44px;
  border-radius: 0;
  text-indent: 0;
  font-size: 24px;
  color: inherit;
  background: transparent!important;
  opacity: .5;
}
.cart .coupon input {
  border: none;
  width: 33.3%;
  margin-right: 5px;
  font-size: 12px;
  line-height: 4;
  padding-left: 20px;
}
.cart .coupon .coupon-code {
  background-color: rgba(0,0,0,0.060);
}
.cart .coupon .apply-btn {
  padding: 0;
  text-align: center;
}
.checkout input {
  background-color: rgba(0,0,0,0.060);
  border: none;
  width: 100%;
  margin-bottom: 15px;
  font-size: 12px;
  line-height: 1.6;
  padding: 17px 20px 17px 20px;
}
.checkout .half-left,
.checkout .half-right {
  padding: 0;
}
@media (min-width:768px) {
  .checkout .half-left {
    width: 50%;
    padding: 0 5px 0 0;
  }
  .checkout .half-right {
    width: 50%;
    padding: 0 0 0 5px;
  }
}
.quantity {
  overflow: hidden;
  display: block;
}
input.qty {
  position: relative;
  float: left;
  height: 50px;
  width: 80px;
  line-height: 50px;
  margin: 0;
  padding: 0;
  background: transparent!important;
  box-shadow: none;
  border-radius: 0;
  text-shadow: none;
  border: 2px solid rgba(0,0,0,0.060);
  border-width: 2px!important;
  z-index: 1;
  text-align: center;
}
.product-details input[type=number]::-webkit-inner-spin-button,
.product-details input[type=number]::-webkit-outer-spin-button {
  border: 2px solid rgba(0,0,0,0.060);
  border-left: 0;
  display: block;
  width: 30px;
  text-align: center;
  line-height: 31px;
  opacity: 1;
}
.qty-adjust {
  float: left;
}

