/*---------- COLORS ----------*/

/*--- Background Colors ---*/
.bg-grey {
  background-color: #f7f7f7;
}
.bg-grey-1 {
  background: #f2f2f2;
}
.bg-grey-2 {
  background: #eee;
}
.bg-dark {
  background: #111!important;
}
.bg-dark-1 {
  background: #1c1c1c;
}
.bg-dark-2 {
  background: #0c0e11;
}
.bg-white {
  background: #fff;
}
.bg-overlay {
  display: block;
  position: relative;
}
.bg-half.bg-overlay {
  display: table;
}
.bg-overlay .container .row > div,
.bg-overlay > div {
  z-index: 3;
}
.bg-overlay:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  opacity: .93;
}
.bg-overlay .back-icon {
  opacity: .1;
}
.border-top {
  border-top: 1px solid #eee;
}
.border-bottom {
  border-bottom: 1px solid #eee;
}

