}

/*---- Coming Soon Page ----*/
.coming-soon {
  margin: 0!important;
  top: 0!important;
}
.coming-soon h1 {
  font-size: 80px;
}
#countdown-timer {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}
#countdown-timer div {
  display: inline-block;
  margin: 15px 20px;
  font-size: 20px;
  text-align: center;
  font-size: 14px;
}
#countdown-timer h1,
#countdown-timer h2,
#countdown-timer h6 {
  font-family: "lovelo", sans-serif;
  font-weight: normal;
  line-height: 1;
}
@media(min-width:1000px){.countdown-big h1 {font-size: 110px;margin: 0 20px;}}
@media(max-width:992px){#countdown-timer h1 {font-size: 40px;}}
.countdown-big h6 {color:#fff;}

