  color: #999;
}
.home-blog-filters .cbp-l-filters-button .cbp-filter-item,
.home-filters-dark .cbp-l-filters-button .cbp-filter-item {
  margin: 20px 0;
  color: #191a1c;
}
.home-portfolio-filters .cbp-filter-item-active.cbp-filter-item,
.home-portfolio-filters .cbp-filter-item:hover {
  color: #fff!important;
}
@media (max-width:992px) {
  .cbp-l-filters-button .cbp-filter-item {
    font-size: 10px;
    padding: 0 10px;
  }
  .home-blog-filters .cbp-l-filters-button .cbp-filter-item,
  .home-filters-dark .cbp-l-filters-button .cbp-filter-item {
    margin: 5px;
  }
}

/*---- Home Event ----*/
.event-speakers {text-align: center}
.event-speakers h4 {
  font-family: "lovelo", sans-serif;
  font-size: 15px;
  margin-top: 20px;
  margin-bottom: 0;
}
.timeline {position: relative;}
.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  left: 6px;
  height: 92%;
  width: 3px;
}
.timeline {
  margin-top: 3em;
  margin-bottom: 0;
}
.timeline::before {
  left: 50%;
  margin-left: -3px;
}
.timeline-block {
  position: relative;
}
.timeline-block:last-child {
  margin-bottom: 0;
  top: 2px;
}
.timeline-bullet {
  position: absolute;
  top: 0;
  left: 0;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  border: 3px solid;
  background: #fff;
}
.timeline-content h2 {
  font-family: "lovelo", sans-serif;
  font-size: 14px;
  line-height: 1.0;
  margin-top: 0;
  margin-bottom: 0;
}
.timeline-bullet {
  left: 50%;
  margin-left: -7px;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
}
.timeline-content {
  margin-left: 0;
  width: 47.3%;
}
.timeline-content {
  position: relative;
  min-height: 50px;
  margin-bottom: 10px;
}
.timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.timeline .timeline-block:nth-child(odd) .timeline-content h2, .timeline .timeline-block:nth-child(odd) .timeline-content p {
  text-align: right;
}
.timeline-block:after {
  content: "";
  display: table;
  clear: both;
}
.timeline-content .date {
  position: absolute;
  width: 100%;
  left: 112%;
  top: 0;
  font-size: 14px;
}
.timeline-block {
  margin: 2em 0;
}
.timeline-block:first-child {
  margin-top: 0;
}
.timeline-block:nth-child(even) .timeline-content .date {
  left: auto;
  right: 112%;
  text-align: right;
}
.timeline-block:nth-child(even) .timeline-content {
  float: right;
}
#clock.event-countdown .digit {
  font-size: 14px;
}
.event-price .price-box h2 sup {
  margin-left: -26px;
}

